.header-nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100px;
    background-color: #ffff;
}

.nav--logo {
    font-weight: 600;
    font-size: 24px;
}

.logo--unwind {
    color: #166527;
}

.logo--cabins {
    color: #1A3724;
}

.nav--menu {
    display: flex;
    gap: 32px;
    font-weight: 400;
    list-style: none;
}

/* burger menu */
@media only screen and (min-width: 900px) {
    .hamburger-react {
        display: none;
    }
}

@media only screen and (max-width: 900px) {
    .header-nav {
        justify-content: space-between;
        padding: 0px 5%;
        height: 70px;
    }

    .nav--menu {
        display: none;
      }
    

    .nav--logo {
        font-size: 20px;
    }
}